// import Lessons from './../mockData/LessonsData'

class LessonService{
    getLessons(){
        let promise =  fetch('http://localhost:4040/api/lessons', {
            method:"GET",
            headers :{
                "Access-Control-Allow-Origin" : "*"
            }
        }).then((response)=>{
            return response.json();
        }).catch(function(err) {
            console.log('Fetch Error :-S', err);
        })

        return promise;
}
}

export default LessonService;