import React, { Component } from 'react';
import FlashcardHeader from './FlashcardHeader';
import FlashcardFooter from './FlashcardFooter';
import FlashcardContent from './FlashcardContent';


class ContentFlashCardComponent extends Component {

    constructor(props) {
         super(props);
        this.state ={
            lesson: []
        }

        // console.log('data '+ JSON.stringify(this.props))
    }

    componentDidMount() {
        this.setState(
            { 
                lesson: this.props.location.query.lesson 
            }
        );
        // console.log('this.props :'+ JSON.stringify(this.props.location.query.lesson))    
    }

    // state = {}
    render() { 
        // console.log('this.props :'+ JSON.stringify(this.state.lesson))    
        return ( 
            <div>
                {/* <h1> This is Content page for Introduction</h1> */}
                {/* <hr/>
                {this.state.lesson.title} */}
                <div className="w3-card-4">
                    <FlashcardHeader header={this.state.lesson.title}/>
                    
                    <FlashcardContent page={JSON.stringify(this.state.lesson)}/>

                    <FlashcardFooter />
                </div>
            </div>
         )
    }
}
 
export default ContentFlashCardComponent;