

class SkillService{

    //get all skills data
    getSkills(){
        console.log('in skillService');
        let promise =  fetch('http://localhost:4040/api/skills', {
            method:"GET",
            headers :{
                "Access-Control-Allow-Origin" : "*"
            }
        }).then((response)=>{
            return response.json();
        }).catch(function(err) {
            console.log('Fetch Error :-S', err);
        })

        return promise;
    }

    // get skill by Skill_Id
    getSkillsBySkillId(skill_Id){
        let promise =  fetch('http://localhost:4040/api/skillById', {
            method:"GET",
            headers :{
                "Access-Control-Allow-Origin" : "*",
                skill_Id: skill_Id
            }
        }).then((response)=>{
            return response.json();
        }).catch(function(err) {
            console.log('Fetch Error :-S', err);
        })

        return promise;
    }
}

export default SkillService;