/* eslint-disable no-unused-expressions */
import React, { Component } from 'react'
import LessonService from '../services/LessonService';
import ListItem from './ListItem';

class LessonList extends Component {
    _lessons;
    constructor(props) {
        super(props);
        this.state = { 
            lessons: []
        }
        this.lessonServ = new LessonService();
    }

    componentDidMount(){
        this.lessonServ.getLessons().then((result)=>{
            this.setState(
                {
                    lessons:result.data
                });
        });
    }
 
    render() { 
        // console.log('lessons data:'+ JSON.stringify(this.state.lessons))
        return ( 
            <div className='listItem'>
                <div className='appTitle'>
                    <h2 className="appTitle">Harbinger Learning App</h2>
                </div>
                {   
                    this.state.lessons.map(
                        (lesson, index) => {
                             return (
                                <div key={index}>
                                    <ListItem key={index} lesson={lesson} />
                                </div>
                            )
                        }
                    )
                }
            </div>
         );
    }
}
 
export default LessonList;