import React, { Component } from 'react';
import SkillService from './../services/SkillService'
import {Link} from 'react-router-dom'

class ListItem extends Component {
    constructor(props) {
        super(props);
        this.state = { 
            lessons : [],
            name: '',
            skills: [],
            skill_img: "",
            skill_name: ""
         }

         this.skillServ = new SkillService();
            
    }

    componentDidMount(){
        console.log('props data :'+ JSON.stringify(this.props.lesson))
        let skill_Id = this.props.lesson.skill_Id;
        this.skillServ.getSkillsBySkillId(skill_Id).then((result)=>{
            this.setState(
                {
                    skills:result.data
                }); 

                console.log('skill data :'+ JSON.stringify(this.state.skills))

            this.setState(
                {
                    skill_img: this.state.skills[0].img,
                    skill_name: this.state.skills[0].name
                })
                console.log('skill_name :'+this.state.skill_name);
        });

        this.setState(
            {
                lessons: JSON.stringify(this.props)
            })

    }

    render() { 
        const { lesson } = this.props;
        // console.log('data:'+ JSON.stringify(this.props.lesson));
        return ( 
            <div className='listItem'>
                <Link to={{ 
                    pathname: `/contentFlashCard`, 
                    query: {
                        lesson: this.props.lesson
                    } 
                    }}>

                    <div>
                        <img src={lesson.image} alt="HarbingerLogo" />
                        <h3 className="lessonTitle"> 
                            {lesson.title}
                        </h3>
                        <img src={this.state.skill_img} alt="topicImage" height="10%"/>
                        <h4 className="skillName">
                            {this.state.skill_name}
                        </h4>
                        <progress value="75" ></progress>
                    </div>
                </Link>
            </div>
         );
    }
}
 
export default ListItem;